$font: 'PT Serif', serif;
$block: 'Open Sans', sans-serif;
$back: #fff;
$text: #3a3a3a;
$nav: #262626;
$caption: #919194;
$primary: #48a2d7;
$sec1: #2880b9;
$sec2: #e74c3c;
$link: #008080;
$notification: #1D8281;
$deadline:  #DB634F;
$yellow: #FCCF4D;
$jhatak: #C3FF00;
*{
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    margin: 0
}

.viz{
    position: relative;
    text-align: center;
}
.labels p{
    position: absolute;
    -moz-transition: all 1s linear;
    -webkit-transition: all 1s linear;
    -o-transition: all 1s linear;
    transition: all 1s linear;
    margin: 0;
    font-family: $block;
    span{
        font-weight: bold;
        font-style: italic;
    }
    span:after{
        content:" options";
    }
    @media(max-width: 500px){
        font-size: 0.9em;
    }
}
.year-text{
    font-size: 2.5em;
    font-weight: bolder;
    text-align: center;
    font-family: $block;
    @media(max-width: 800px){
        font-size: 2em;
    }
    @media(max-width: 600px){
        font-size: 1.5em;
        color: #13294A
    }
}


a{
    color: inherit;
}

img.graphic{
  width: 95%;
  margin: 1em auto;
}
.img-container{
  background-color: #1a1a1a;
  text-align: left;
  @media(max-width: 700px){
    text-align: center;
  }
}
.ins.copy{
    color: #6a6a6a;
    font-style: italic;
    margin: 0.5em auto;
    font-size: 1.1em;
    @media(max-width: 800px){
        font-size: 0.9em

    }
    @media(max-width: 600px){
        font-size: 0.8em;
        line-height: 1.1em;
    }
}

.hide-desktop{
    display: none;
    @media(max-width: 700px){
        display: block;
    }
}

p.chart-title,p.chart-subhed{
    font-family: Open Sans;
    margin-bottom: 0.1em!important;
    text-align: left!important;
}


.chart-title{
    font-size: 1.1em;
    line-height: 1.3em;
    font-weight: bold;
    color: #3a3a3a;
    padding-bottom: 0.2em;
    font-family: $block;

}

.chart-subhed{
    font-size: 0.9em;
    font-family: $block;
    line-height: 1.2em;
    font-style: italic;
    color: #3a3a3a;
    padding-bottom: 0.5em;
}

.hide-mobile{
    display: block;
    @media(max-width: 700px){
        display: none;
    }
}

body{ 
    font-family: $font;
    background-color: $back;
    color: $text;
    position: relative;
}

.copy{
    width: 91%;
    margin-left: auto;
    margin-right: auto;
    max-width: 700px;
    font-size: 1.25em;
    line-height: 1.5em;
    color: $text;
    a{
        color: $link
    }
    @media(max-width: 700px){
        line-height: 1.6em;
        font-size: 1.1em;
    }
}

.back-text{
  padding: 1px 0.3em;
  color: #fff;
  border-radius: 2px;
}

.copy-container{
    margin-top: 4em;
    p{
        margin-bottom: 1.5em;
    }
    @media(max-width:1300px){
        padding-top: 0.5em;
    }
    .copy.methodology{
        margin-top: 2em;
        font-size: 1em;
        line-height: 1.4em;
        h3{
            padding-bottom:0.5em;
            margin-bottom:0.5em;
            border-bottom: solid 2px $primary;
        }
        p{
            @media(max-width: 500px){
                font-size: 0.9em;
                color: #888;
            }
        }
    }
} 

.story{
    padding-top: 40px;
    .headline-container{
        text-align: center;
        max-width: 800px;
        margin: 0 auto;
        @media(max-width: 700px){
            width: 95%;
            margin-left: auto;
            margin-right: auto;
        }
        @media(max-width: 500px){
            font-size: 0.9em;
        }
        @media(max-width: 400px){
            font-size: 0.8em;
        }
    }
    .subhed{
        padding-bottom: 2em;

        font-style: italic;
        font-weight: normal;
    }
    .playins{
        padding-bottom: 0.3em;
        padding-top: 1.5em;
        text-transform: uppercase;
        font-family: $block;
        font-weight: 700;
        color: $sec1;

    }
    .sentence{
        text-align: center;
    }
    .play-button{
        font-size: 2em;
        padding: 0.1em;
        width: 100px;
        margin: 0 auto 1em auto;
        border-bottom: 1px solid #ddd;
        border-top: 1px solid #ddd;
    }
    .play-button:hover{
        color: $sec1;
        cursor: pointer;
    }
    .headline{
        padding-top: 0.7em;
        font-size: 2.3em;
        margin-left: auto;
        margin-right: auto;
        
        padding-bottom: 0.7em;
        span{
            display: block;
            font-size: 0.6em;
            font-weight: bold;
            padding-bottom: 0.4em;
            text-transform: uppercase;
            color: $primary;
            font-family: $block;
        }
        @media(max-width: 600px){
            font-size: 2.1em;
        }
    }
}


h3.byline{
    font-weight: 500;
    margin-bottom: 0.8em;
    font-size: 1.2em;
}

.byline{
    .line{
        width: 220px;
        margin: 1em auto;
        border: 2px solid $primary;
    }
}

h3.byline.dateline{
     font-size: 1.1em;
}

.nav {
    color: white;
    height: 40px;
    z-index: 1000;
    background-color: $nav;
    width: 100%;
    padding: 0.2em;
    /*position: fixed;*/
    a{
        color: inherit;
    }
    .nav-logo {
        padding-left: .3em;
        vertical-align: middle;
        height: 60%;
        margin-top: 4px;
    }
    .nav-logo.mobile {
        display: none;
    }
    .nav-logo.desktop {
        vertical-align: middle;
        display: inline-block;
    }
    .right-container {
        position: absolute;
        right: 28px;
        top: 4px;
        font-size: 1.4em;
        i{
            margin-left: 8px;
        }
        @media(max-width: 750px){
            right: 14px;
        }
    }
    @media(max-width: 750px){
        .nav-logo.mobile {
            display: inline-block;
        }
        .nav-logo.desktop {
            display: none;
        }
    }
}

section{
    h2{
        padding-bottom: 0.5em;
        border-bottom: solid 4px $primary;
        margin-bottom: 0.5em;
        font-size: 1.7em;
        line-height: 1.4em;
    }
}


.related-stories.copy{
    margin-top: 2em;
    padding-top: 30px;
    max-width: 1000px;
    border-top: 1px solid #ddd;
    h2{
    border-bottom: none;
    font-size: 1.4em;
    }
    .series {
        text-transform: uppercase;
        font-size: .8em;
        padding-bottom: .1em;
        font-weight: 700;
        color: #2880b9;
    }
    img{
        width: 220px;
        @media(max-width: 700px){
            width: 90%;
        }
    }
    a{
        color: inherit;
        text-decoration: none;
        vertical-align: top;
        cursor: pointer;
    }

    .related-story{
        max-width: 210px;
        display: inline-block;
        margin-right: 2em;
        vertical-align: top;
        text-align: left;

        @media(max-width: 700px){
            margin-right: auto;
            margin-bottom: 2em;
            margin-left: auto;
            width: 90%;
            max-width: 100%;
        }
    }
    .related-story:hover{
        opacity: 0.8;
    }
    h3{
      font-size: 0.9em;
      span{
        font-style: italic;
        font-weight: bold;
      }
      @media(max-width: 700px){
        font-size: 1em;
      }
    }
    @media(max-width: 700px){
        text-align: center;
    }
}

.footer-basic-centered {
    background-color: $nav;
    color: #fff;
    width: 100%;
    text-align: center;
    font: 300 20px;
    padding: 30px;
    padding-bottom: 50px;

    margin-top: 30px;
    .footer-links{
        float: right;
    }
    .footer-company-name{
        float: left;
    }
    .footer-links,.footer-company-name{
        vertical-align: middle;
        padding-bottom: 20px;
    }
    a{
        margin-left: 5px;
        margin-right: 5px;
    }
    @media(max-width: 800px){
          padding-bottom: 100px;
    }
}
.course.highlight{
    stroke: #3a3a3a;
}

.copy.intro{
    text-align: center;
    font-size: 1.2em;
    @media(max-width: 750px){
        text-align: left;
    font-style: normal;
    font-size: 1.1em;
    padding: 0 1em;
    }
}

/*tooltips*/
.tip {
  border-radius: 3px;
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.5);
  background-color: #fff;
  border: 1px solid #ccc;
  position: fixed;
  padding: 5px;
  width: 200px;
  text-align: left;
  font-size: 0.8em;
  font-family: $block;
}
.sentence{
    padding: 1em 1em 1.5em 1em ;
    font-family: $block;
    font-size: 1.2em;
    background-color: #eee;
    @media(max-width: 700px){
        padding: 0.5em 0.5em 1em 0.5em ;
        font-size: 1em;
    }

}
.fa-play:hover,.fa-pause:hover{
    color: #ccc;
    cursor: pointer;
}
.sentence.track{
    background-color: #C3FF00;
    color: black;
}

.tip:before {
  z-index: 1;
  position: absolute;
  left: 50%;
  margin-left: -15px;
  content: '';
  width: 0;
  height: 0;
}
.tip.top:before {
  top: 98.1%;
  border-top: solid 10px #fff;
  border-left: solid 10px transparent;
  border-right: solid 10px transparent;
}
.tip.bottom:before {
  bottom: 100%;
  border-bottom: solid 10px #fff;
  border-left: solid 10px transparent;
  border-right: solid 10px transparent;
}

.tip .quantity,.tip .location{
    font-family: "Open Sans";
    font-size: 0.8em;
}

#slider-container{
    width:100%;
    margin:0 auto .5em; 
    max-width: 800px;
    text-align: center;
}

.play-button-new{
    display: inline-block;
    font-size: 2em;
    padding-left: 0.5em;
    @media(max-width: 800px){
    font-size: 1.2em;

    }
}

#slider{padding:0;margin:0;width:100%;
    display: inline-block;
}

#play-pause,.play-button{display:inline-block}
#play-pause{margin-bottom:4px;margin-left:10%;height:25px;cursor:pointer}
.labels{margin-bottom:10px}.slider-input{display:inline-block;width:70%}
.slider-label-min{font-size:12px;float:left;display:inline}
.slider-label-max{font-size:12px;float:right;display:inline}
@-moz-document url-prefix(){#play-pause{margin-bottom:-1px}}

.social-shares{text-align: center; margin:1.5em 0 0 auto;font-size: 0.8em; font-family:PT Serif,serif}.social-shares .social{border-radius:6px;padding:.2em 0.6em;width:150px;display:inline-block;text-align:left;color:#fff;font-style:italic;font-size:1.3em;margin:0 10px;cursor:pointer}.social-shares .social i{font-size:1.2em;vertical-align:middle;margin-right:20px}.social-shares .social.facebook{background-color:#3b5998;border:2px solid #3b5998}.social-shares .social.twitter{background-color:#1da1f2;border:2px solid #1da1f2}.social-shares .social.whatsapp{background-color:#43d854;border:2px solid #43d854;display:none}.social-shares .social:hover{background-color:#fafafa!important}.social-shares .social.facebook:hover{color:#3b5998}.social-shares .social.twitter:hover{color:#1da1f2}.social-shares .social.whatsapp:hover{color:#43d854}
@media (max-width:800px){
    .social-shares {text-align: center; font-size: 1em;}.social-shares .social.whatsapp{background-color:#43d854;display:inline-block}.social-shares .social{width:auto;padding:.2em .8em}.social-shares .social i{margin-right:0}.social-shares .social span{display:none}.social-shares .social{margin:0 5px;border-radius:4px}}


